.popup {
    margin: 0;
    padding: 0;
    width: 100%;
    min-height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    display: flex;
    visibility: hidden;
    opacity: 0%;
    transition: .4s ease-in-out;
}