.footer__copyright {
    margin: 0;
    padding: 0;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #545454;
}

@media (max-width: 500px) {
    .footer__copyright {
        font-size: 14px;
        line-height: 17px;
        max-width: 282px;
        margin-left: auto;
        margin-right: auto;
    }
}